import { getSheetList, getNewSong } from "@/api/recommend-api";
import { OK } from "@/assets/js/config";
import RecommendFooter from "public/Footer.vue";
import MusicList from '@/components/Musiclist.vue';
export default {
  data: function data() {
    return {
      sheetList: [],
      // 推荐歌单
      isLoading: true,
      tempSongs: [],
      musicD: {
        song: [],
        red: false,
        SQ: true,
        rank: false
      }
    };
  },
  components: {
    RecommendFooter: RecommendFooter,
    MusicList: MusicList
  },
  created: function created() {
    this._getSheetList();

    this._getNewSong();
  },
  computed: {
    songs: function songs() {
      return this.tempSongs.map(function (item) {
        return {
          id: item.id,
          name: item.name,
          alias: item.song.alias,
          ar: item.song.artists,
          al: {
            name: item.song.album.name
          },
          copyright: item.song.copyright
        };
      });
    }
  },
  methods: {
    selectList: function selectList(id) {
      this.$router.push({
        path: 'playlist/detail',
        query: {
          id: id
        }
      });
    },
    _getSheetList: function _getSheetList() {
      var _this = this;

      getSheetList().then(function (res) {
        if (res.status === OK) {
          _this.sheetList = res.data.result.splice(0, 6);
        } else {
          console.log("获取推荐歌单失败");
        }
      });
    },
    _getNewSong: function _getNewSong() {
      var _this2 = this;

      getNewSong().then(function (res) {
        if (res.status === OK) {
          _this2.isLoading = false;
          _this2.tempSongs = res.data.result;
          _this2.musicD.song = _this2.songs;

          _this2.$store.dispatch('playlist/setMusicList', _this2.musicD);
        }
      });
    }
  }
};