import axios from 'axios';
import { HOST } from 'js/config';

function getSheetList() {
  // 获取推荐歌单
  var url = HOST + '/personalized';
  return axios.get(url);
}

function getNewSong() {
  // 最新音乐
  var url = HOST + '/personalized/newsong';
  return axios.get(url);
}

function getSheetDetails(id) {
  var url = HOST + "/playlist/detail?id=".concat(id);
  return axios.get(url);
}

export { getSheetList, getNewSong, getSheetDetails };